<template>
  <div>
    <a-row type="flex" justify="space-around">
      <a-col :span="11">
        <a-card title="满天星计划数据录入————企业发布岗位页">
          <a-form
            :form="form"
            :label-col="{ span: 8 }"
            :wrapper-col="{ span: 10 }"
            @submit="handleSubmit"
          >
            <a-form-item label="岗位名称">
              <a-input v-decorator="['jobName', { rules: [{ required: true, message: '请输入！' }] }]" />
            </a-form-item>
            <a-form-item label="企业名称">
              <a-input v-decorator="['enterpriseName', { rules: [{ required: true, message: '请输入！' }] }]" />
            </a-form-item>
            <!--<a-form-item label="所在省市">
              <a-cascader v-decorator="['provinceAndCity', { rules: [{ required: true, message: '请输入！' }] }]" :options="options" />
            </a-form-item>-->
            <a-form-item label="所在省级行政区">
              <a-input v-decorator="['province', { rules: [{ required: true, message: '请输入！' }] }]" placeholder="北京市/河北省" />
            </a-form-item>
            <a-form-item label="所在市级行政区">
              <a-input v-decorator="['city', { rules: [{ required: true, message: '请输入！' }] }]" placeholder="昌平区/石家庄市" />
            </a-form-item>
            <a-form-item label="需求人数">
              <a-input-number v-decorator="['jobNumber', { rules: [{ required: true, message: '请输入！' }] }]" :min="1" />
            </a-form-item>
            <a-form-item label="报名时间区间">
              <a-range-picker v-decorator="['rangePicker', { rules: [{ type: 'array', required: true, message: '请选择！' }], },]" />
            </a-form-item>
            <a-form-item label="联系方式">
              <a-input v-decorator="['enterpriseContact', { rules: [{ required: true, message: '请输入！' }] }]" />
            </a-form-item>
            <a-form-item label="工作地点">
              <a-input v-decorator="['workplace', { rules: [{ required: true, message: '请输入！' }] }]" />
            </a-form-item>
            <a-form-item label="工作内容">
              <a-textarea v-decorator="['jobDuty', { rules: [{ required: true, message: '请输入！' }] }]" :rows="4" />
            </a-form-item>
            <a-form-item label="岗位要求">
              <a-textarea v-decorator="['jobRequirement', { rules: [{ required: true, message: '请输入！' }] }]" :rows="4" />
            </a-form-item>
            <a-form-item label="薪资待遇">
              <a-input v-decorator="['jobSalary', { rules: [{ required: true, message: '请输入！' }] }]"  />
            </a-form-item>
            <a-form-item label="福利待遇">
              <a-input v-decorator="['jobBenefit', { rules: [{ required: true, message: '请输入！' }] }]" />
            </a-form-item>
            <a-form-item label="其他岗位详情说明">
              <a-textarea v-decorator="['jobSpecification', { initialValue: ''}]" placeholder="选填" :rows="4" />
            </a-form-item>
            <a-form-item label="企业类型">
              <a-select v-decorator="['enterpriseType',{ rules: [{ required: true, message: '请选择！' }] },]">
                <a-select-option value="教育岗">教育岗</a-select-option>
                <a-select-option value="企业岗">企业岗</a-select-option>
                <a-select-option value="政务岗">政务岗</a-select-option>
                <a-select-option value="乡村岗">乡村岗</a-select-option>
                <a-select-option value="公益岗">公益岗</a-select-option>
              </a-select>
            </a-form-item>
            <a-form-item label="企业介绍">
              <a-textarea v-decorator="['enterpriseIntroduction', { rules: [{ required: true, message: '请输入！' }] }]" :rows="4" />
            </a-form-item>
            <a-form-item label="补充说明">
              <a-textarea v-decorator="['moreInfo', { initialValue: ''}]" placeholder="选填" :rows="4" />
            </a-form-item>
            <a-form-item :wrapper-col="{ span: 8, offset: 9 }">
              <a-button type="primary" html-type="submit">提交</a-button>
            </a-form-item>
          </a-form>
        </a-card>
      </a-col>
      <a-col :span="11">
        <a-card title="当前已有记录(只显示部分信息)">
          <a-table :columns="column" :data-source="data" :rowKey="'id'" :pagination="{ pageSize: 8 }">
            <a-popconfirm
              slot="delete"
              slot-scope="text, record"
              title="确定要删除该条记录？"
              ok-text="是"
              cancel-text="否"
              @confirm="deleteOneRecord(record.id)"
            >
              <a-button type="danger" :size="'small'">删除</a-button>
            </a-popconfirm>
          </a-table>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import buct from '../components/buct'
//import {provinceAndCityData, CodeToText} from 'element-china-area-data'
const column = [
  {
    title: "岗位名称",
    dataIndex: "jobName",
  },
  {
    title: "企业名称",
    dataIndex: "enterpriseName",
  },
  {
    title: "需求人数",
    dataIndex: "jobNumber",
  },
  {
    title: "企业类型",
    dataIndex: "enterpriseType",
  },
  {
    title: "操作",
    scopedSlots: { customRender: "delete" },
  },
];
export default {
  name: "index",
  data() {
    return {
      data: [],
      formLayout: "horizontal",
      form: this.$form.createForm(this, { name: "jobRelease" }),
      column,
      //options: provinceAndCityData,
    };
  },
  components: {
    buct,
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          //console.log(values)
          let postObject = {
            "jobName": values['jobName'],
            "enterpriseName": values['enterpriseName'],
            //"province": CodeToText[values['provinceAndCity'][0]],
            //"city": CodeToText[values['provinceAndCity'][1]],
            "province": values['province'],
            "city": values['city'],
            "jobNumber": values['jobNumber'],
            "startTime": new Date(values['rangePicker'][0].format('YYYY-MM-DD')).getTime(),
            "endTime": new Date(values['rangePicker'][1].format('YYYY-MM-DD')).getTime(),
            "enterpriseContact": values['enterpriseContact'],
            "workplace": values['workplace'],
            "jobDuty": values['jobDuty'],
            "jobRequirement": values['jobRequirement'],
            "jobSalary": values['jobSalary'],
            "jobBenefit": values['jobBenefit'],
            "jobSpecification": values['jobSpecification'],
            "enterpriseType": values['enterpriseType'],
            "enterpriseIntroduction": values['enterpriseIntroduction'],
            "moreInfo": values['moreInfo'],
          }
          /*let postObject = {
            "jobName":"ycl",
            "enterpriseName":"aaa",
            "jobNumber":2,
            "startTime":1624976599003,
            "endTime":1624976599000,
            "enterpriseContact":"123456",
            "workplace":"aaa",
            "jobDuty":"aaa",
            "jobRequirement":"aaa",
            "jobSalary":"aaa",
            "jobBenefit":"aaa",
            "jobSpecification":"aaa",
            "enterpriseType":"aaa",
            "enterpriseIntroduction":"aaa",
            "moreInfo":"",
          }*/
          console.log(postObject)
          this.$store.state
            .jobReleaseDataPost(postObject)
            .then((response) => {
              console.log(response.data);
              if(response.data == 1){
                alert("提交成功！")
                this.$router.go(0);
              }
              else{
                alert("提交失败！请联系开发者");
              }
          });
        } else {
          alert("提交失败！请检查是否有必填字段未填");
        }
      });
    },
    deleteOneRecord(id){
      this.$store.state
        .jobReleaseDataDelete(id)
        .then((response) => {
          console.log(response.data);
        });
      alert('删除成功!')
      this.$router.go(0);
    },
    getBackendData() {
      this.$store.state
        .jobReleaseDataGet()
        .then((response) => {
          this.data = response.data;
          console.log(this.data);
        });
    },
  },
  beforeMount() {
    this.getBackendData();
  },
};
</script>

<style scoped>

</style>